export default {
    name:"any-icon",
    props:{
        type:{
            type:String,
            required:true,
            default:'editer',
            description:''
        },
        size:{
            type:String,
            required:false,
            default:'1em',
            description:''
        },
        color:{
            type:String,
            required:false,
            default:'#000000',
            description:''
        },
        fontFamily:{
            type:String,
            required:false,
            default:'any-icon',
            description:''
        },
        useSVG:{
            type:Boolean,
            required:false,
            default:false,
            description:'svg'
        }
    },
    data(){
        return {}
    },
    computed:{
        className:function(){
            return `${this.fontFamily}-${this.type}`;
        }
    },
    components:{
        
    }
}