import echarts from 'echarts';
import config from './ChartPie.config.js';
const props = config.properties.reduce((p, n) => {
    if (!p[n.props]) {
        p[n.props] = {
            required: false,
            default: function() {
                return n['value'];
            }
        }
    }
    return p;
}, {});
export default {
    name: 'custom-chart-pie',
    inject:['componentWraper'],
    props: props,
    data() {
        return {}
    },
    computed: {
        options: function() {}
    },
    mounted() {
        var container = this.$refs.container;
        var chartInstance = echarts.init(container);
        container.dataset.chartInstance = chartInstance;
        chartInstance.setOption({
            title: {
                text: '',
                subtext: '',
                x: 'center'
            },
            tooltip: {
                trigger: 'item',
                formatter: "{a} <br/>{b} : {c} ({d}%)"
            },
            legend: {
                orient: 'vertical',
                left: 'left',
                data: ['', '', '', '', '']
            },
            series: [{
                name: '',
                type: 'pie',
                radius: '55%',
                center: ['50%', '60%'],
                data: [{
                    value: 335,
                    name: ''
                }, {
                    value: 310,
                    name: ''
                }, {
                    value: 234,
                    name: ''
                }, {
                    value: 135,
                    name: ''
                }, {
                    value: 1548,
                    name: ''
                }],
                itemStyle: {
                    emphasis: {
                        shadowBlur: 10,
                        shadowOffsetX: 0,
                        shadowColor: 'rgba(0, 0, 0, 0.5)'
                    }
                }
            }]
        });
        this.componentWraper.$on('onComponentResizing',function(size){
            chartInstance.resize(size);
        });
    },
    watch: {
        '$parent.width': function(val, old) {
            console.log('width-change', val, old);
        }
    }
}