/**
 * 
 * @param {String} string 
 * @param {Number} min 
 * @param {Number} max 
 * @returns {Boolean}  
 */
export function length(string='',min=0,max=256){
    if(typeof string !== 'string'){
        console.error(`input param of 'string' is not string type`);
        return;
    }
    return string.length >= min && string.length <= max;
}
/**
 * 
 * @param {String} string 
 * @returns {Boolean}  
 */
export function email(string){
    if(typeof string !== 'string'){
        console.error(`input param of 'string' is not string type`);
        return;
    }
    let reg = /^\w+(?:[\d|\w])*@[\w|\d]+\.[\w|\d]+$/;
    return reg.test(string);
}

/**
 * 
 * @param {String} string 
 * @returns {Boolean}  
 */
export function number(string){
    if(typeof string === 'number'){
        return true;
    }
    if(typeof string !== 'string'){
        console.error(`input param of 'string' is not string type`);
        return;
    }
    if(string.trim() === ''){
        return false;
    }else if(string.trim() === '.'){
        return false;
    }
    let reg = /^\d*\.{0,1}\d*$/;
    return reg.test(string);
}

