import {fromEvent} from 'rxjs/internal/observable/fromEvent';
import {map,distinctUntilChanged} from 'rxjs/operators';
export default {
    name:'any-input',
    inject:{
        formInstance:{
            default:null
        },
        formItemInstance:{
            default:null
        }
    },
    props:{
        type:{
            type:String,
            required:false,
            default:'text',
            description:''
        },
        default:{
            type:String,
            required:false,
            default:'',
            description:'reset'
        },
        value:{
            type:String|Number,
            required:false,
            default:'',
            description:''
        },
        lazy:{
            type:Boolean,
            required:false,
            default:false,
            description:''
        }
    },
    data(){
        return {
            defaultValue:'',
            error:false
        };
    },
    methods:{
        updateFormModel(value){
            this.formInstance.model[this.formItemInstance.prop] = value;
        },
        reset(){
            this.formInstance.model[this.formItemInstance.prop] = this.defaultValue;
            this.$el.value = this.defaultValue;
        }
    },
    mounted(){
        this.defaultValue = this.default;
        /*****************input***************/
        fromEvent(this.$el,'input')
        .pipe(map(event=>{
            return event.target.value
        }))
        .pipe(distinctUntilChanged())
        .subscribe(value=>{
            this.updateFormModel(value);
            this.$emit('inputChange',value);
        });
        /*****************input***************/

        if(this.formItemInstance.prop !== ''){
            this.$el.value = this.formInstance.model[this.formItemInstance.prop];
            this.$watch(`formInstance.model.${this.formItemInstance.prop}`,(val,old)=>{
                this.$el.value = val;
            });
        };
        if(this.formInstance){
            this.formInstance.$on('reset',()=>{
                this.reset();
                this.$emit('inputChange',this.defaultValue);
            });
            this.formInstance.$on('clear',()=>{
                this.updateFormModel('');
                this.$emit('inputChange','');
            });
            this.formInstance.$on('validate-error',()=>{
                this.error = true;
            });
        }
    }
}