export default {
    name:'any-alert',
    props:{
        message:{
            type:String,
            required:true,
            default:'',
            description:''
        },
        type:{
            type:String,
            required:false,
            default:'error',
            description:',(success|info|warning|error)'
        },
        icon:{
            type:String,
            required:false,
            default:'',
            description:''
        },
        showIcon:{
            type:Boolean,
            required:false,
            default:false,
            description:''
        }
    }
}