import CrisisgoIcon from '../Icon/Icon.component.vue';
export default {
    name:'any-button',
    props:{
        disabled:{
            type:Boolean,
            required:false,
            default:false,
            description:''
        },
        type:{
            type:String,
            required:false,
            default:'primary',
            description:''
        },
        icon:{
            type:String,
            required:false,
            default:'',
            description:''
        }
    },
    data(){
        return {}
    },
    computed:{
        typeClass(){
            return `button-${this.type}`
        }
    },
    components:{
        CrisisgoIcon
    }

}