import Vue from 'vue';
import {componentHasCache,cacheComponentConfig,getComponentConfig} from '@/services/componentConfig.js'

const requireComponent = require.context(
  // 
  './base',
  // 
  true,
  // 
  /[A-Z]\w+\.component\.vue$/
);
const requireComponentConfig =require.context(
  // 
  './base',
  // 
  true,
  // 
  /[A-Z]\w+\.config\.js$/
);

requireComponent.keys().forEach(fileName => {
  // 
  const component = requireComponent(fileName);
  const componentName = component.default.name;
  const configName = fileName.replace(/component\.vue$/,'config.js');
  const config = requireComponentConfig(configName);
  if(!componentHasCache(componentName)){
    cacheComponentConfig(componentName,config.default);
    console.log('getComponentConfig',getComponentConfig(componentName));
  }
  // 
  Vue.component(
    componentName,
    //  `export default` 
    //  `.default`
    // 
    component.default || component
  )
});