export default {
    name:'any-form-item',
    provide(){
        return {
            formItemInstance:this
        }
    },
    inject:['formInstance'],
    props:{
        prop:{
            type:String,
            required:false,
            default:'',
            description:''
        },
        required:{
            type:Boolean,
            required:false,
            default:false,
            description:''
        },
        label:{
            type:String,
            required:false,
            default:'',
            description:'lable'
        },
        labelWidth:{
            type:Number,
            required:false,
            default:100,
            description:'label'
        },
        labelTextAlign:{
            type:String,
            required:false,
            default:'right',
            description:'label,text-align'
        },
        labelAlignItems:{
            type:String,
            required:false,
            default:'center',
            description:'label,flexalign-items'
        }
    },
    data(){
        return {
            
        }
    },
    computed:{
        labelStyle(){
            return {
                width:this.labelWidth+'px',
                textAlign:this.labelTextAlign
            }
        }
    },
    methods:{
        reset(){
            this.value = ''
        }
    },
    mounted(){
        this.formInstance.$on('reset',()=>{
            this.reset();
        })
    }
}